import React, { ReactElement, useEffect, useState } from 'react';
import './menubar.less';
import { ComponentProps, GetAltColor, GetComponentClassNames } from '../../../utils/PiziComponent/PiziComponent';
import { ClassNameHelper } from '../../../utils/Utils';
import { ButtonGroup } from '../../Controls/ButtonGroup/ButtonGroup';
import { Button } from '../../Controls/Button/Button';
import { MenuItem } from './MenuItem';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export interface MenuBarProps extends ComponentProps{
	logo?: ReactElement
	user?: string
	screens: ReactElement[]
	open?: boolean
	onSwitch?: (open: boolean) =>void
	menuTop?: React.ReactElement
	menuBottom?: React.ReactElement
}

/**
 * MenuBar UI component
 */
export const MenuBar: React.FC<MenuBarProps & React.HTMLAttributes<HTMLDivElement>> = React.memo(({
	screens,
	user,
	open = false,
	onSwitch = () => null,
	...props
}) => {
	const[openState, setOpenState] = useState(open);
	const[closing, setClosing] = useState(false);
	
	const toggleMenu = (openMenu: boolean) => {
		if(openMenu) {
			setOpenState(true)
			onSwitch(true)
		} else {
			setClosing(true)
		}
		
	}

	useEffect(() => {
		if(!openState)scrollTo(0,0)
	}, [openState])

	useEffect(() => {
		if(!openState && open) {
			setOpenState(true)
		} else if(openState && !open){
			setClosing(true)
		}
	}, [open])

	return <div className={ClassNameHelper("pizi-menubar", {"open": openState})}>
				<div className={ClassNameHelper(GetComponentClassNames("pizi-menubar__bar border-alt-light", props))}>
					<div className="pizi-menubar__bar__logo">
						{props.logo}
					</div>
					{
						user && <div className="pizi-menubar__bar__user">
									<FontAwesomeIcon icon="user"/>
									<div>
										<label>user</label>
										<span>{user}</span>
									</div>
								</div>
					}
					<ButtonGroup {...{color: props.color, appearance: props.appearance}}>
						<Button icon="bars" size="large" align="right" className="menu-button" onClick={() => toggleMenu(!openState)}/>
					</ButtonGroup>
				</div>
				<div className={ClassNameHelper(GetComponentClassNames("pizi-menubar__container animate__animated fill", props), {
					[props.color]: !!props.color,
					"animate__fadeInRight": openState,
					"animate__fadeOutRight": closing,
				})} onAnimationEnd={() => {
					if(closing){
						setClosing(false)
						setOpenState(false)
						onSwitch(false)
					}
				}}>
					{
						props.menuTop
					}
					<nav className={ClassNameHelper("pizi-menubar__container__items", {[GetAltColor(props.color)]: props.color})}>
					{
						screens.filter(screen => !screen.props.hideInMenu).map((screen, index) => 	<MenuItem path={screen.props.path} 
																											icon={screen.props.icon} 
																											key={"menu-link-" + index}
																											onClick={() => toggleMenu(false)}>
																										{screen.props.title}
																									</MenuItem>)
					}
					</nav>
					{
						props.menuBottom
					}
				</div>
			</div>
})