import * as React from 'react'
import {Button, TextInput} from 'pizi-react'
import { RouteComponentProps } from '@reach/router'
import './password-reset.less'
import { InputValidation } from 'pizi-react/src/components/Inputs/TextInput/TextInput'
import { PiziUsers } from '../../utils/PiziServer'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

interface IPasswordResetCodeProps extends RouteComponentProps{
  urlCode?: string
}

export const PasswordResetCode: React.FunctionComponent<IPasswordResetCodeProps> = (props) => {

  const[form, setForm] = React.useState({checkCode: "", password: "", checkPassword:"", urlCode: props.urlCode})
  const[error, setError] = React.useState(true)
  const[status, setStatus] = React.useState("")
  const[errorMessage, setErrorMessage] = React.useState("")

  return  <>
            <div hidden={status === "success"}>
              <p>Enter the code received by email, and type a new password.</p>
              <TextInput type="text" 
                        label="Code" 
                        valdationRegex={InputValidation.code.regex} 
                        valdationMessage={InputValidation.code.message}
                        onChange={checkCode =>{setForm({...form, checkCode})}}
                        error={status === "error" ? errorMessage : ""}
                        onError={setError}/>
              <TextInput type="password" 
                        label="New password" 
                        onChange={password =>{setForm({...form, password})}} 
                        onError={setError}/>
              <TextInput type="password" 
                        label="Confirm new password" 
                        onChange={checkPassword =>{setForm({...form, checkPassword})}} 
                        onError={setError}
                        error={status === "password" ? errorMessage : ""}/>
              <Button disabled={error} onClick={() => {
                if(form.password !== form.checkPassword){
                  setStatus("password")
                  setErrorMessage("passwords not matching!")
                  return
                }

                !error && PiziUsers.resetPassword(form).then(() => setStatus("success")).catch(() => {
                  setStatus("error")
                  setErrorMessage("wrong code")
                })}
              }>Change password</Button>
            </div>
            <p hidden={status !== "success"} className="success animate__animated animate__fadeIn"><FontAwesomeIcon icon="check" className="animate__animated animate__tada"/>If you're email is associated to an account, an email as been sent with a code to reset your password!</p>
          </>
}

