import React, {Component } from 'react'
import { Home } from './screens/Home/Home'
import { Login } from './screens/Login/Login'
import { RestUI } from './screens/RestUI/RestUI'
import { RestUIDetail } from './screens/RestUI/RestUIDetail'
import { PiziToken } from './utils/PiziServer'
import { PasswordResetCode } from './screens/Password/PasswordResetCode'
import { PasswordReset } from './screens/Password/PasswordReset'
import { MenuApp } from 'pizi-react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { navigate } from "@reach/router"
import { Footer } from './components/Footer/Footer'
import { PasswordResetForm } from './screens/Password/PasswordResetForm'

type AppProps = {
    socket: any
}

type AppState = {
    infos: any
    token: any
    menuOpen: boolean
}

const basepath = location.pathname.match(/^\/([^\/]+\/)/)[1]

export class App extends Component<AppProps, AppState> {

    constructor(props: AppProps){
        super(props);
        this.state = {
            menuOpen: null,
            token: {},
            infos: {
                apps: [],
                log:{},
                rest: {},
                jwt: false,
                db: "initializing...",
                tokenUrl: "",
                tokenExpire: "",
                https: false
            }
        }
    }

    componentDidMount(){
        // Try to reconnect
        this.props.socket.on("connect", () => {
            // TODO: implement
        })

        this.props.socket.on("infos", (infos :any) => this.setState({infos}))
        PiziToken.checkToken().then(token => token && this.setState({token})).catch(console.log)
    }
    
    render(){
        return  <>
                    <MenuApp    color="main"
                                open={this.state.menuOpen}
                                appearance="fill"
                                logo={<FontAwesomeIcon icon="server" onClick={e => {
                                    navigate("/")
                                    this.setState({menuOpen: false})
                                }}/>}
                                onSwitch={menuOpen =>this.setState({menuOpen})}
                                basepath={basepath}
                                user={this.state.token?.user}>
                        <Home   title="HOME"
                                default
                                path="/"
                                icon="home"
                                infos={this.state.infos} 
                                token={this.state.token}/>
                        <Login  title="LOGIN"
                                path="login"
                                icon="user"
                                onLogged={token => this.setState({token})} 
                                token={this.state.token}/>
                        <PasswordReset  title="PASSWORD RESET"
                                        path="password-reset"
                                        icon="key">
                            <PasswordResetForm path="/"/>
                            <PasswordResetCode path=":urlCode"/>
                        </PasswordReset>
                        <RestUI title="REST UI"
                                path="pizi-rest-ui"
                                icon="hdd">
                                <RestUIDetail path=":collectionId"/>
                        </RestUI>
                    </MenuApp>
                    <Footer/>
                </>  
    }
}