import { IconName } from "@fortawesome/fontawesome-svg-core";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";
import { ComponentProps, GetComponentClassNames } from "../../../utils/PiziComponent/PiziComponent";
import './menu-item.less';
import { Link } from '@reach/router';

export interface MenuItemProps extends ComponentProps{
    icon?: IconName
    path?: string
}

/**
 * MenuBar UI component
 */
export const MenuItem: React.FC<MenuItemProps & React.HTMLAttributes<HTMLDivElement>> = ({
    icon,
    path,
	...props
}) =>   <Link   className={GetComponentClassNames("pizi-menu-item", {...props, appearance: null})}
                to={path} 
                {...props}>
            <FontAwesomeIcon icon={icon}/>
            <label>{props.children}</label>
        </Link>