import React, { ReactElement, useEffect, useState, useCallback, useRef} from 'react';
import { CleanProps, ClassNameHelper } from '../../../utils/Utils';
import { Button } from '../../Controls/Button/Button';
import { Pagination } from '../../Controls/Pagination/Pagination';
import { ComponentProps, GetComponentClassNames, defaultProps, GetAltColor } from '../../../utils/PiziComponent/PiziComponent';
import './table.less';
import { library } from '@fortawesome/fontawesome-svg-core';
import {faSortAmountDownAlt} from '@fortawesome/free-solid-svg-icons/faSortAmountDownAlt';
import {faSortAmountUp} from '@fortawesome/free-solid-svg-icons/faSortAmountUp';
library.add(faSortAmountDownAlt, faSortAmountUp);

export interface TableOrder {
    direction: 'up' |'down'
    header: any
}

export interface TableProps extends Omit<ComponentProps, 'size'>{
    header: string[]
    data: any[][]
    defaultOrder?: TableOrder
    selectable?: boolean
    onSelected?: (selection: any[]) => void
    sort?: {[key:string]: (a: any, b:any) => number}
    staticHeader?: boolean
    sortIcon?: "default" | "small" 
}

/**
 * Primary UI component for user interaction
 */
export const Table: React.FC<TableProps> = React.memo(({
    header,
    data,
    defaultOrder = {direction: null, header: null},
    selectable = true,
    onSelected = () => {},
    sort = {},
    staticHeader = false,
    sortIcon = "default",
	...other
}) => {
    const props = {...defaultProps, ...other}
    const orderedData = [...data]
    const [order, setOrder] = useState(defaultOrder)
    const [currentData, setData] = useState(orderedData)
    const [pagination, setPagination] = useState(null)
    const [selected, setSelected] = useState(null)
   
    const orderTable = useCallback((order: TableOrder) => {
        if(order.direction) orderedData.sort((a, b) => {
            let itemA = a[header.indexOf(order.header)]
            let itemB = b[header.indexOf(order.header)]

            if(sort[order.header]) return sort[order.header](itemA, itemB)

            if(typeof itemA === 'string') itemA = itemA.trim().toLowerCase()
            if(typeof itemB === 'string') itemB = itemB.trim().toLowerCase()
            return itemA > itemB ? 1 : itemA < itemB ? -1 : 0
        })
        if(order.direction === "up") orderedData.reverse()

        if(!pagination){
            setData(orderedData)
        } else {
            setPagination(React.cloneElement(pagination, {
                data: orderedData,
                onChange: setData,
                ...props
            }))
        }

    }, [order, pagination, orderedData])

    const orderIcon = () => {
        switch(sortIcon){
            case "small":
                return order.direction === "down" ? "long-arrow-alt-down" : "long-arrow-alt-up"
            case "default":
            default:
                return order.direction === "down" ? "sort-amount-down-alt" : "sort-amount-up"
        }
    }

    const clickHandler = selectable ? (line: string[]) => {
        const selection = line.toString() !== selected ? line : null
        setSelected(selection && selection.toString())
        onSelected(selection)
    } : undefined

    useEffect(() =>{
        orderTable(order)
        setPagination((() => {
            let paginationEl: ReactElement;
            // Check Children (Pagination)
            React.Children && React.Children.forEach(props.children, child => {
                if(!React.isValidElement(child)) throw new Error("Cannot allow non valid react element children inside table!")
                if(child.type !== Pagination) throw new Error("Cannot allow children of type " + child.type + " inside table!")

                paginationEl = React.cloneElement(child, {
                    data: orderedData,
                    onChange: setData,
                    ...props
                })
            }) 
            return paginationEl
        })())
    }, []);
    
    useEffect(() =>{},[props.color, props.appearance])
    useEffect(() =>{
       orderTable(order)
    },[data, order])
    useEffect(() => {
        if((defaultOrder.header === null && defaultOrder.direction === null) 
        ||(order.header === defaultOrder.header && order.direction === defaultOrder.direction)) return
        setOrder(defaultOrder)
    },[defaultOrder])

	return  <div className={ClassNameHelper(GetComponentClassNames('pizi-table', props))} {...CleanProps(props)}>
                <div className={ClassNameHelper("pizi-table-container", {"static-header": staticHeader})}>
                    <table> 
                        <thead>
                            <tr>
                                {
                                    header.map((item) => <th className={"border"} key={item} color={props.color}>
                                                            <Button {...props}
                                                            className={ClassNameHelper("head-cell no-active", {
                                                                                    order: order.direction && order.header === item,
                                                                                    "small-sort": sortIcon === "small"
                                                                                })} 
                                                            color={props.appearance === "fill" && GetAltColor(props.color) || props.color}
                                                            onClick={() => setOrder((order && order.direction === "down" && order.header === item) ? {direction: "up", header: item} : {direction: "down", header: item})}
                                                            appearance="simple"
                                                            iconRight={orderIcon()}>
                                                                {item}
                                                            </Button>
                                                        </th>)
                                }
                            </tr>
                        </thead>
                        <tbody>
                                {
                                    currentData.map((line) =><tr onClick={selectable ? (e) => clickHandler(line) : undefined}
                                                                key={line.toString()}
                                                                className={ClassNameHelper("", {
                                                                    "selected fill alt": selected === line.toString()
                                                                })}>
                                                            {
                                                                line.map((data, index) => <td key={index}>{data}</td>)
                                                            }
                                                            </tr>)
                                }
                        </tbody>
                    </table>
                </div>
                {pagination}
            </div>
})
