import * as React from 'react'
import {Button, TextInput} from 'pizi-react'
import './password-reset.less'
import { InputValidation } from 'pizi-react/src/components/Inputs/TextInput/TextInput'
import { PiziUsers } from '../../utils/PiziServer'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { RouteComponentProps } from '@reach/router'


interface IPasswordResetFormProps extends RouteComponentProps{

}

export const PasswordResetForm: React.FunctionComponent<IPasswordResetFormProps> = (props) => {

  const[email, setEmail] = React.useState("")
  const[emailSent, setSentEmail] = React.useState(false)
  const[error, setError] = React.useState(false)

  return <>
            <TextInput  type="text" 
                        label="Email" 
                        readOnly={emailSent}
                        valdationRegex={InputValidation.email.regex} 
                        valdationMessage={InputValidation.email.message} 
                        onChange={setEmail}
                        onError={setError}/>
            <Button disabled={!email} display={!emailSent} onClick={() => {
                if(error) return
                PiziUsers.resetPassword({email}).catch()
                setTimeout(() => setSentEmail(true))
            }}>Reset password</Button>
            <p hidden={!emailSent} className="success animate__animated animate__fadeIn"><FontAwesomeIcon icon="check" className="animate__animated animate__tada"/>If you're email is associated to an account, an email as been sent with a code to reset your password!</p>
            {props.children}
        </>
}

