import React from 'react';
import './form-input.less';
import { ComponentProps, GetComponentClassNames } from './PiziComponent';
import { ClassNameHelper } from '../Utils';

export interface FormInputProps extends  Omit<ComponentProps, "defaultValue" |"onChange" |"onError" | "appearance">{
    className?: string
	inputName?: string
    readOnly?: boolean
    label?: string
    error?: string
    autoFocus?: boolean 
    key?: any
    appearance?: 'default' | 'alt'
}

/**
 * Base Form Input UI component
 */
export const FormInput: React.FC<FormInputProps & React.HTMLAttributes<HTMLDivElement>> = ({
    label,
    className,
    inputName,
    color = "main",
    appearance = 'default',
	...props
}) => {

	return <div key={props.key}
                className={ClassNameHelper("pizi-input", inputName, {
                                                                        error: !!props.error, 
                                                                        alt: appearance === "alt", 
                                                                        hidden: props.display === false,
                                                                        [props.size]: props.size
                                                                    }, className)}>
				<label className={color}>{label}</label>
				<div className={inputName + "__input"}>
					{props.children}
				</div>
                <div className="pizi-input__error error">
                    {props.error}
                </div>
	        </div>
}