import React from 'react'
import './menuapp.less'
import { ComponentProps } from '../../../utils/PiziComponent/PiziComponent'
import { ClassNameHelper } from '../../../utils/Utils'
import { Router } from '@reach/router'
import { MenuBar, MenuBarProps } from './MenuBar'

export interface MenuAppProps extends ComponentProps, Omit<MenuBarProps, "screens">{
	basepath?: string
}

/**
 * MenuBar UI component
 */
export const MenuApp: React.FC<MenuAppProps & React.HTMLAttributes<HTMLDivElement>> =  React.memo(({
	basepath = "/",
	...props
}) => {

	const screens: any[] = []
	const extraElements: any[] = []
	React.Children && React.Children.forEach(props.children, (child, index) => {
		if(!React.isValidElement(child)) throw new Error("Cannot allow non valid react element children inside menubar!")
		if(child.props.path){
			screens.push(React.cloneElement(child, {key: "screen-" + index, path: basepath + child.props.path}))
		} else {
			extraElements.push(child)
		}
	})

	return 	<div className={ClassNameHelper("pizi-menu-app")}>
				<MenuBar screens={screens} {...props}>
				{extraElements}
				</MenuBar>
				<Router primary={false} className="pizi-menu-app-container">
				{screens}
				</Router>
	        </div>
})