import { AppScreenProps, Button, TextInput } from 'pizi-react'
import React, { useState } from 'react'
import { PiziToken } from '../../utils/PiziServer'

interface LoginProps extends AppScreenProps{
    className?: string
    onLogged?: (token: string) => void
    token: any
}
 
export const Login = ({onLogged, token = {}}:LoginProps) => {
    const[username, setUsername] = useState("");
    const[password, setPassword] = useState("");
    const[error, setError] = useState("")

    const login = () => {
        if(token.jwt){
            token = {};
            PiziToken.clearToken();
            onLogged && onLogged(token);
        } else {
            PiziToken.getToken(username, password).then(token => {
                if(!token) return
                if(token.exist) setError("Wrong password!")
                if(token.jwt) onLogged && onLogged(token);
            })
        }
    }

    return  <div className={"login pizi-container"}>
                <h1>Account login</h1>
                <p>Log to an existing account or create a new one!</p>
                <TextInput label="login" display={!token.jwt} onChange={setUsername} />
                <TextInput label="password" error={error} display={!token.jwt} type="password" onChange={setPassword}/>
                <a>reset password!</a>
                <Button onClick={login}>{ token.jwt ? "sign out" : "sign in"}</Button>
                <Button appearance="border">create new account</Button>
            </div>
}