import { ClassNameHelper } from '../Utils'
import { FormInputProps } from './FormInput'

export type Theme = 'default' |'alt'
export type Appeareance = 'fill' | 'border' | 'simple'
export type Size = 'small' | 'medium' | 'large'
export type Color = 'main' | 'secondary' | 'success' | 'warn' | 'error' | 'blue'

export interface ComponentProps extends Omit<React.HTMLAttributes<HTMLElement>,"display">{
	display?: boolean
	appearance?: Appeareance
	size?: Size
	color?: Color
	key?: any
}

export const defaultProps: any = {
	display: true,
	appearance: 'fill',
	size: 'medium',
	color: 'main'
}

export const GetComponentClassNames = (classname: string = "", {
	appearance = defaultProps.appearance, 
	size = defaultProps.size,
	display = defaultProps.display,
	color,
	className = ""
}: ComponentProps | FormInputProps) => ClassNameHelper(classname, className, {
	hidden: !display,
	[appearance]: !!appearance,
	[size]: !!size,
	[color]: !!color
})

export const GetAltColor = (color: string) => {
	switch(color){
		case "main":
		return "secondary"
		case "secondary":
		return "main"
		case "success-alt":
		return "success"
		default:
		return "secondary"
	}
}